import { useCallback, Fragment } from "react";
import { axios } from "../commons";

const Test = () => {
  const onClickButton = useCallback(async () => {
    await axios("get", {}, "https://rest.metacoin.network:20923/balance/MTCEzFvqsQIXrnA2TgNxnjv5Mi6C6MdFab0a6f88").then((res) => {
      console.log(res);
    });
  }, []);

  return (
    <Fragment>
      <section className="bg_dark">
        <div className="container">
          <button type="button" className="btn btn_blue" onClick={onClickButton}>
            테스트
          </button>
        </div>
      </section>
    </Fragment>
  );
};

export default Test;
